.CreateCategoryContainer {
    display: flex;
    
    .CreateCategoryContainer-Left {
      flex: 0 0 20%;
      padding-right: 10px;
    }
  
    .CreateCategoryContainer-Right {
      flex: 1;
      
      .CreateCategoryContainer-Right-Heading {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 20px;
      }
  
      .CreateCategoryContainer-Right-Form {
        margin-bottom: 20px;
      }
  
      .CreateCategoryContainer-Right-Content {
        .CreateCategoryContainer-Right-Table {
          width: 100%;
          border-collapse: collapse;
          margin-bottom: 10px;
          
          th,
          td {
            padding: 10px;
            border: 1px solid #ddd;
          }
  
          th {
            background-color: #f2f2f2;
            font-weight: bold;
          }
  
          tbody tr:nth-child(even) {
            background-color: #f9f9f9;
          }
          
          .CreateCategoryContainer-Right-Table-Editbtn,
          .CreateCategoryContainer-Right-Table-Deletebtn {
            padding: 5px 10px;
            margin-right: 5px;
            border-radius: 4px;
            font-size: 14px;
            cursor: pointer;
          }
  
          .CreateCategoryContainer-Right-Table-Editbtn {
            background-color: #28a745;
            color: #fff;
            border: none;
          }
  
          .CreateCategoryContainer-Right-Table-Deletebtn {
            background-color: #dc3545;
            color: #fff;
            border: none;
          }
        }
      }
    }
  }
  
  @media (max-width: 600px) {
    .CreateCategoryContainer {
      flex-direction: column;
      
      .CreateCategoryContainer-Left {
        flex: none;
      }
      
      .CreateCategoryContainer-Right {
        flex: none;
        margin-top: 20px;
        
        .CreateCategoryContainer-Right-Form {
          margin-bottom: 10px;
        }
      }
    }
  }
  