.UpdateProductContainer {
  display: flex;

  .UpdateProductContainer-Left {
    flex: 0 0 20%;
    padding-right: 10px;
  }

  .UpdateProductContainer-Right {
    flex: 1;

    .UpdateProductContainer-Right-Heading {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .UpdateProductContainer-Right-Content {
      .UpdateProductContainer-RightForm-select {
        width: 100%;
        margin-bottom: 20px;
      }

      .UpdateProductContainer-Right-PhotoUploadContainer {
        margin-bottom: 20px;

        .UpdateProductContainer-Right-PhotoUploadLebel {
          display: block;
          border: 1px solid #ddd;
          border-radius: 4px;
          padding: 10px;
          background-color: #fff;
          cursor: pointer;

          input[type="file"] {
            display: none;
          }
        }
      }

      .UpdateProductContainer-Right-PhotoDisplayContainer {
        margin-bottom: 20px;

        .UpdateProductContainer-Right-PhotoDisplayURLContainer {
          img.UpdateProductContainer-Right-PhotoDisplayURL {
            width: 100%;
            max-height: 200px;
            object-fit: cover;
            border: 1px solid #ddd;
            border-radius: 4px;
          }
        }

        .UpdateProductContainer-Right-PhotoDisplayImgContainer {
          img.UpdateProductContainer-Right-PhotoDisplayImg {
            width: 100%;
            max-height: 200px;
            object-fit: contain;
            border: 1px solid #ddd;
            border-radius: 4px;
          }
        }
      }

      .UpdateProductContainer-Right-Name,
      .UpdateProductContainer-Right-Desc,
      .UpdateProductContainer-Right-Price,
      .UpdateProductContainer-Right-Oldprice,
      .UpdateProductContainer-Right-Rating,
      .UpdateProductContainer-Right-Quantity {
        margin-bottom: 20px;

        input[type="text"],
        input[type="number"],
        textarea {
          width: 100%;
          border: 1px solid #ddd;
          border-radius: 4px;
          padding: 10px;
          background-color: #fff;
        }

        textarea {
          resize: vertical;
          height: 100px;
        }
      }

      .UpdateProductContainer-Right-SelectContainer {
        margin-bottom: 20px;

        .UpdateProductContainer-Right-Select {
          width: 100%;
        }
      }

      .UpdateProductContainer-Right-UpdateBtnDiv,
      .UpdateProductContainer-Right-DeleteBtnDiv {
        margin-bottom: 20px;

        .UpdateProductContainer-Right-UpdateBtn,
        .UpdateProductContainer-Right-DeleteBtn {
          width: 100%;
          padding: 10px;
          border: none;
          border-radius: 4px;
          color: #fff;
          font-weight: bold;
          cursor: pointer;
        }

        .UpdateProductContainer-Right-UpdateBtn {
          background-color: #4b79db;

          &:hover {
            background-color: #003cbd;
          }
        }

        .UpdateProductContainer-Right-DeleteBtn {
          background-color: #d9534f;

          &:hover {
            background-color: #c9302c;
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .UpdateProductContainer {
    flex-direction: column;

    .UpdateProductContainer-Left {
      flex: none;
    }

    .UpdateProductContainer-Right {
      flex: none;
      margin-top: 20px;
    }
  }
}
