.Ordersdashboard {
  display: flex;

  .Ordersdashboard-Left {
    flex: 0 0 20%;
    padding-right: 10px;
  }

  .Ordersdashboard-Right {
    flex: 1;

    .Ordersdashboard-Right-Heading {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .Ordersdashboard-Right-TableContainer {
      margin-bottom: 40px;

      .Ordersdashboard-Right-Table {
        width: 100%;
        border-collapse: collapse;
        font-size: 16px;
        @media (max-width: 600px) {
          font-size: 10px;
        }
        th,
        td {
          padding: 12px;
          border: 1px solid #ccc;

          @media (max-width: 600px) {
            padding: 0;
          }
        }

        th {
          text-align: left;
        }

        tbody {
          tr {
            &:nth-child(even) {
              background-color: #f9f9f9;
            }

            td {
              &:first-child {
                width: 5%;
              }
            }
          }
        }
      }

      .Ordersdashboard-Right-Table-Content {
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;

        .Ordersdashboard-Right-Table-Content-Card {
          width: 50%;
          padding: 10px;

          @media (max-width: 768px) {
            width: 100%;
          }

          .Ordersdashboard-Right-Table-Content-Card-ImgContainer {
            width: 50%;
            height: 300px;
            overflow: hidden;
            margin-right: 10px;

            @media (max-width: 1600px) {
               height: 220px;
            }
            @media (max-width: 1200px) {
               height: 180px;
            }
            @media (max-width: 990px) {
               width: 70%;
            }
            @media (max-width: 768px) {
               width: 50%;
               height: 230px;
            }
            @media (max-width: 650px) {
               height: 220px;
            }
            @media (max-width: 500px) {
               width: 60%;
            }
            @media (max-width: 460px) {
               width: 70%;
            }
            @media (max-width: 400px) {
               width: 80%;
            }
            @media (max-width: 340px) {
               width: 100%;
            }
            img {
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }

          .Ordersdashboard-Right-Table-Content-Card-Text {
            h5 {
              font-size: 18px;
              font-weight: bold;
              margin-bottom: 5px;
            }

            p {
              margin-bottom: 5px;
              color: #555;
            }

            h6 {
              font-size: 14px;
              font-weight: bold;
              color: #ff0000;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .Ordersdashboard {
    flex-direction: column;

    .Ordersdashboard-Left {
      flex: none;
    }

    .Ordersdashboard-Right {
      flex: none;
      margin-top: 20px;
    }
  }
}
