.faq-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 40px;
  
    .title {
      font-size: 28px;
      font-weight: bold;
      margin-bottom: 20px;
    }
  
    .faq-item {
      margin-bottom: 30px;
      position: relative;
  
      .question {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 10px;
      }
  
      .answer {
        font-size: 16px;
      }
  
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #e8e8e8;
        bottom: -15px;
        left: 0;
      }
  
      &:after {
        content: "";
        position: absolute;
        width: 0;
        height: 2px;
        background-color: #4c79ff;
        bottom: -15px;
        left: 0;
        transition: width 0.3s ease-in-out;
      }
  
      &:hover:after {
        width: 100%;
      }
    }
  }
  