.SearchContainer {
  padding: 20px;
  width: 80%;
  margin: 0 auto;
  @media (max-width: 768px) {
    width: 100%;
    padding: 20px 10px;
  }

  .SearchContainer-Content {
    .SearchContainer-Content-Heading {
      font-size: 24px;
      font-weight: bold;
      color: #333;
      margin-bottom: 10px;
      text-align: center;
    }

    .SearchContainer-Content-Found-Text {
      font-size: 16px;
      color: #999;
      margin-bottom: 20px;
      text-align: center;
    }

    .SearchContainer-Content-Container {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 20px;
      @media (max-width: 1500px) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media (max-width: 990px) {
        grid-template-columns: repeat(2, 1fr);
      }

      @media (max-width: 550px) {
        grid-template-columns: 1fr;
      }

      .SearchContainer-Content-Container-Card {
        background-color: #fff;
        border-radius: 8px;
        overflow: hidden;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        transition: transform 0.3s ease;

        &:hover {
          transform: translateY(-5px);
        }

        .SearchContainer-Content-Container-Card-Img-Container {
          position: relative;
          overflow: hidden;
          width: 100%;
          height: 350px;
          display: flex;
          align-items: center;
          justify-content: center;

          @media (max-width: 1024px) {
            height: 250px;
          }
          @media (max-width: 550px) {
            height: 380px;
          }
          @media (max-width: 380px) {
            height: 250px;
          }
        }

        .SearchContainer-Content-Container-Card-img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          cursor: pointer;
        }

        .productPageCard-AddtocartIcon {
          position: absolute;
          top: 10px;
          right: 10px;
          background-color: #5640b9;
          color: #fff;
          border: none;
          border-radius: 50%;
          width: 40px;
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: background-color 0.3s;

          &:hover {
            background-color: #2f00ff;
          }
        }

        .SearchContainer-Content-Container-Card-body {
          padding: 10px;

          .SearchContainer-Content-Container-Card-title {
            font-size: 16px;
            color: #333;
            margin-bottom: 5px;
            cursor: pointer;
          }

          .SearchContainer-Content-Container-Card-Prices-Container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 5px;

            .SearchContainer-Content-Container-Card-Price {
              font-size: 14px;
              color: #333;
            }

            .SearchContainer-Content-Container-Card-Oldprice {
              font-size: 12px;
              color: #999;
              text-decoration: line-through;
            }

            .SearchContainer-Content-Container-Card-Discount {
              font-size: 12px;
              background-color: #1c1c1c;
              color: #fff;
              padding: 2px 8px;
              border-radius: 10px;
            }
          }

          .SearchContainer-Content-Container-Card-Rating {
            font-size: 12px;

            &-Span {
              background-color: #ff0000;
              color: #fff;
              padding: 2px 8px;
              border-radius: 12px;
            }
          }

          .SearchContainer-Content-Container-Card-Free-Text {
            font-size: 12px;
            color: #333;
          }
        }
      }
    }
  }
}
