.CreateProductContainer {
    display: flex;
    
    .CreateProductContainer-Left {
      flex: 0 0 20%;
      padding-right: 10px;
    }
  
    .CreateProductContainer-Right {
      flex: 1;
      
      .CreateProductContainer-Right_heading {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 20px;
      }
  
      .CreateProductContainer-Right-Content {
        .CreateProductContainer-Right-FormSelect {
          width: 100%;
          margin-bottom: 20px;
        }
  
        .CreateProductContainer-Right-PhotoUploadCotaniner {
          margin-bottom: 20px;
          
          .CreateProductContainer-Right-PhotoUploadCotaninerLabel {
            display: inline-block;
            padding: 10px 20px;
            background-color: #f2f2f2;
            border-radius: 4px;
            cursor: pointer;
            
            input[type="file"] {
              display: none;
            }
          }
        }
  
        .CreateProductContainer-Right-PhotoDisplayCotaniner {
          margin-bottom: 20px;
          
          .CreateProductContainer-Right-PhotoDisplayCotaninerImageDisplay {
            display: inline-block;
            padding: 10px;
            border: 1px solid #ddd;
            border-radius: 4px;
            
            .CreateProductContainer-Right-PhotoDisplayCotaninerImageDisplayImg {
              max-width: 100%;
              height: auto;
            }
          }
        }
  
        .CreateProductContainer-Right-ProductNameContainer,
        .CreateProductContainer-Right-ProductDescContainer,
        .CreateProductContainer-Right-ProductPriceContainer,
        .CreateProductContainer-Right-ProductOldPriceContainer,
        .CreateProductContainer-Right-ProductRatingContainer,
        .CreateProductContainer-Right-ProductQuantityContainer,
        .CreateProductContainer-Right-ProductShippingContainer,
        .CreateProductContainer-Right-ProductCreatebtnContainer {
          margin-bottom: 20px;
        }
  
        .CreateProductContainer-Right-ProductName,
        .CreateProductContainer-Right-ProductDesc,
        .CreateProductContainer-Right-ProductPrice,
        .CreateProductContainer-Right-ProductOldPrice,
        .CreateProductContainer-Right-ProductRating,
        .CreateProductContainer-Right-ProductQuantity,
        .CreateProductContainer-Right-ProductShipping {
          width: 100%;
          padding: 10px;
          border: 1px solid #ddd;
          border-radius: 4px;
          font-size: 16px;
        }
  
        .CreateProductContainer-Right-ProductCreatebtn {
          padding: 10px 20px;
          background-color: #28a745;
          color: #fff;
          border: none;
          border-radius: 4px;
          font-size: 16px;
          cursor: pointer;
        }
      }
    }
  }
  
  @media (max-width: 600px) {
    .CreateProductContainer {
      flex-direction: column;
      
      .CreateProductContainer-Left {
        flex: none;
      }
      
      .CreateProductContainer-Right {
        flex: none;
        margin-top: 20px;
      }
    }
  }
  