.page-not-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  
    .page-not-found-title {
      font-size: 120px;
      font-weight: bold;
      color: #f44336;
      margin-bottom: 20px;
      animation: titleAnimation 1s ease-in-out infinite alternate;
    }
  
    .page-not-found-heading {
      font-size: 24px;
      font-weight: bold;
      color: #333;
      margin-bottom: 20px;
    }
  
    .page-not-found-btn {
      font-size: 16px;
      font-weight: bold;
      color: #fff;
      background-color: #f44336;
      border: none;
      border-radius: 4px;
      padding: 10px 20px;
      text-decoration: none;
      transition: background-color 0.3s ease;
  
      &:hover {
        background-color: #e53935;
      }
    }
  }
  
  @keyframes titleAnimation {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
  