.Category-Container {
  padding: 20px;
  width: 80%;
  margin: 0 auto;
  @media (max-width: 768px) {
    width: 100%;
    padding: 20px 10px;
  }
  .Category-Container-Category_name {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 10px;
    text-align: center;
  }

  .Category-Container-Category_foundtext {
    font-size: 16px;
    color: #999;
    margin-bottom: 20px;
    text-align: center;
  }

  .Category-Container-Category-Content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    @media (max-width: 1500px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media (max-width: 990px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 550px) {
      grid-template-columns: 1fr;
    }

    .Category-Container-Category-Content-Card {
      background-color: #fff;
      border-radius: 8px;
      overflow: hidden;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      transition: transform 0.3s ease;

      &:hover {
        transform: translateY(-5px);
      }

      .Category-Container-Category-Content-Card-Img-Container {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 350px;
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: 1024px) {
          height: 250px;
        }
        @media (max-width: 550px) {
          height: 380px;
        }
        @media (max-width: 380px) {
          height: 250px;
        }
      }

      .Category-Container-Category-Content-Card-Img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        cursor: pointer;
      }

      .Category-Container-Category-Content-Card-AddtocartIcon {
        position: absolute;
        top: 10px;
        right: 10px;
        background-color: #5640b9;
        color: #fff;
        border: none;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
          background-color: #2f00ff;
        }
      }

      .Category-Container-Category-Content-Card-body {
        padding: 10px;

        .card-title {
          font-size: 16px;
          color: #333;
          margin-bottom: 5px;
          cursor: pointer;
        }

        .Category-Container-Category-Content-Card-name-prices {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 5px;

          .Category-Container-Category-Content-Card-price {
            font-size: 14px;
            color: #333;
          }

          .Category-Container-Category-Content-Card-Oldprice {
            font-size: 12px;
            color: #999;
            text-decoration: line-through;
          }

          .Category-Container-Category-Content-Card-Discount {
            font-size: 12px;
            background-color: #1c1c1c;
            color: #fff;
            padding: 2px 8px;
            border-radius: 10px;
          }
        }

        .Category-Container-Category-Content-Card-Rating {
          font-size: 12px;
          span {
            background-color: #ff0000;
            color: #fff;
            padding: 2px 8px;
            border-radius: 12px;
          }
        }
      }
    }
  }
}
