$primary-color:#1c1c1c;
$gray-color:gray;

.terms-and-conditions-page {
  padding: 20px;

  .terms-and-conditions-container {
    max-width: 800px;
    margin: 0 auto;

    .title {
      font-size: 32px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .intro {
      font-size: 16px;
      color: $gray-color;
      margin-bottom: 30px;
    }

    .section {
      margin-bottom: 30px;

      .section-title {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 10px;
        color: $primary-color;
      }

      .section-content {
        font-size: 16px;
        color: currentColor;
      }
    }
  }
}
