.Profiledashboard {
    display: flex;
  
    .Profiledashboard-Left {
      flex: 0 0 20%;
      padding-right: 10px;
    }
  
    .Profiledashboard-Right {
      flex: 1;
  
      .Profiledashboard-RightForm-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
  
        .Profiledashboard-RightForm {
          width: 80%;
  
          .Profiledashboard-RightForm-Heading {
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 20px;
          }
  
          .Profiledashboard-RightForm-Name,
          .Profiledashboard-RightForm-Email,
          .Profiledashboard-RightForm-Password,
          .Profiledashboard-RightForm-Phone,
          .Profiledashboard-RightForm-Address {
            margin-bottom: 15px;
  
            input[type="text"],
            input[type="email"],
            input[type="tel"],
            input[type="password"] {
              width: 100%;
              padding: 10px;
              font-size: 16px;
              border: 1px solid #ccc;
              border-radius: 4px;
              transition: border-color 0.3s ease;
            }
  
            input[type="text"]:focus,
            input[type="email"]:focus,
            input[type="password"]:focus,
            input[type="tel"] {
              outline: none;
              border-color: #06bcee;
            }
          }

          .Form-InpPassword {
            display: flex;
            align-items: center;
            position: relative;
      
            .password-icon {
              position: absolute;
              right: 10px;
            }
          }
  
          .Profiledashboard-RightForm-UPDATEBtn {
            display: block;
            width: 100%;
            padding: 10px;
            font-size: 16px;
            font-weight: bold;
            text-align: center;
            color: #fff;
            background-color: #06bcee;
            border: none;
            border-radius: 4px;
            cursor: pointer;
            transition: background-color 0.3s ease;
  
            &:hover {
              background-color: #0498d6;
            }
          }
        }
      }
    }
  }
  
  @media (max-width: 600px) {
    .Profiledashboard {
      flex-direction: column;
  
      .Profiledashboard-Left {
        flex: none;
      }
  
      .Profiledashboard-Right {
        flex: none;
        margin-top: 20px;
      }
    }
  }
  