.productDetailsContainer {
  display: flex;
  align-items: center;
  justify-content: space-around;
  // margin-top: 100px;

  @media (max-width: 800px) {
    flex-direction: column;
  }

  .productDetailsContainerImgContainer {
    width: 50%;
    height: 500px;
    padding: 50px 200px;
    @media (max-width: 1500px) {
      padding: 20px 50px;
    }
    @media (max-width: 800px) {
      width: 90%;
      padding: 0 50px;
    }

    @media (max-width: 460px) {
      width: 100%;
      padding: 0 10px;
    }
    overflow: hidden;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;

    .productDetailsContainerImg {
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: transform 0.3s ease-in-out;

      &:hover {
        transform: scale(1.1);
      }
    }
  }

  .productDetailsContainerContent {
    width: 50%;
    padding-right: 100px;

    @media (max-width: 1024px) {
      width: 90%;
      padding-right: 50px;
    }
    @media (max-width: 800px) {
      width: 90%;
      padding-right: 0px;
    }
    .productDetailsContainerContentHeading {
      font-size: 28px;
      font-weight: 900;
      color: #333;
      margin-bottom: 10px;
    }

    .productDetailsContainerContentName {
      font-size: 24px;
      color: #333;
      margin-bottom: 10px;
      font-weight: 700;
    }

    .productDetailsContainerContentPrice {
      font-size: 20px;
      color: #ff0000;
      margin-bottom: 10px;
      font-weight: 700;
    }

    .productPageCard-Oldprice {
      font-size: 16px;
      color: gray;
      text-decoration: line-through;
    }

    .productPageCard-Discount {
      font-size: 16px;
      background-color: #1c1c1c;
      color: #fff;
      padding: 2px 8px;
      border-radius: 12px;
    }
    .productPageCard-Rating {
      font-size: 16px;
      .productPageCard-Rating-Span {
        background-color: #c93636;
        color: #fff;
        padding: 2px 8px;
        border-radius: 12px;
      }
      margin-top: 10px;
      margin-bottom: 10px;
    }
    .productDetailsContainerContentCatName {
      font-size: 16px;
      color: #333;
      margin-bottom: 10px;
    }
    .productDetailsContainerContentBtns {
      display: flex;
      justify-content: space-evenly;

      @media (max-width: 340px) {
        flex-direction: column;
      }
      .productDetailsContainerContentAddtocartBtn {
        padding: 10px 20px;
        font-size: 18px;
        font-weight: bold;
        color: #fff;
        background-color: #5640b9;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease-in-out;
        text-transform: uppercase;

        &:hover {
          background-color: #2f00ff;
        }
      }
    }

    .productDetailsContainerContentBuynowBtn {
      padding: 10px 20px;
      font-size: 18px;
      font-weight: bold;
      color: #fff;
      background-color: #c43f3f;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s ease-in-out;
      text-transform: uppercase;
      @media (max-width: 340px) {
       margin-top: 20px;
      }
      &:hover {
        background-color: #852727;
      }
    }
  }
}
.productDetailsContainerContentDesc {
  margin-top: 20px;
  padding: 0 50px;

  @media (max-width: 480px) {
    padding: 0 10px;
  }
  summary {
    font-size: 20px;
    color: #333;
    cursor: pointer;
  }

  p {
    white-space: pre-wrap;
    font-size: 16px;
    color: #666;
    margin-top: 10px;
  }
}

.similarProductsContainer {
  text-align: center;
  margin-top: 40px;

  &Heading {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    display: inline-block;
  }

  & svg {
    font-size: 24px;
    vertical-align: middle;
    margin-left: 5px;
  }

  &Text {
    font-size: 16px;
    color: #666;
    margin-top: 10px;
  }

  .productPageContentProducts {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;

    .productPageContentProductsCard {
      width: 250px;
      height: 390px;
      margin: 10px;
      border: 1px solid #ccc;
      border-radius: 5px;
      overflow: hidden;
      transition: transform 0.3s ease-in-out;
      cursor: pointer;
      &:hover {
        transform: scale(1.05);
      }
      .productPageContentProductsCardImgContainer {
        width: 100%;
        height: 230px;
        display: flex;
        align-items: center;
        justify-content: center;

        .productPageCardImg {
          width: 80%;
          height: 100%;
          object-fit: contain;
          cursor: pointer;
        }
      }

      .productPageCard-body {
        padding: 10px;

        .productPageCard-title {
          font-size: 18px;
          color: #333;
          margin-bottom: 5px;
        }

        .productPageCard-Prices {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 0;

          .productPageCard-price {
            font-size: 16px;
            color: #ff0000;
            font-weight: 500;
          }

          .productPageCard-Oldprice {
            font-size: 14px;
            color: #999;
            text-decoration: line-through;
          }

          .productPageCard-Discount {
            font-size: 14px;
            background-color: #1c1c1c;
            color: #fff;
            padding: 2px 8px;
            border-radius: 12px;
          }
        }
        .productPageCard-Rating {
          .productPageCard-Rating-Span {
            background-color: #c93636;
            color: #fff;
            padding: 2px 8px;
            border-radius: 12px;
            .productPageCard-Rating-Spanicon{
              font-size: 16px;
              margin: 0;
              padding: 0;
            }
          }
          margin-top: 0;
          margin-bottom: 0;
        }
      }
    }
  }
}
