.footerPage {
  background: linear-gradient(#3b3d41 0%, #333436 50%, #000000 100%);
  color: #ffffff; 
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  padding: 3rem 0;
  position: relative;
  z-index: 9999;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;

  .footerPageContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding-left: 50px;
    .footerPageContainerChild {
      flex: 1;

      &:not(:last-child) {
        margin-right: 2rem;
      }

      .footerPageContainerChildHeadingPara {
        h4 {
          font-size: 1.5rem;
          margin-bottom: 1rem;
          color: #a6ff00;
        }

        p {
          font-size: 1rem;
          line-height: 1.5;
        }
      }

      .footerPageContainerChildLinks {
        h4 {
          font-size: 1.5rem;
          margin-bottom: 1rem;
          color: #a6ff00;
        }

        .footerlinksContainer {
          display: flex;
          justify-content: space-between;

          .footerPageContainerChildUL-links,
          .footer-links {
            flex: 1;
            list-style: none;
            margin: 0;
            padding: 0;

            li {
              margin-bottom: 1rem;

              a {
                color: #ffffff;
                text-decoration: none;
                transition: color 0.3s ease;

                &:hover {
                  color: #a6ff00;
                }
              }
            }
          }
        }
      }

      .footerPageContainerChildContactInfo {
        h4 {
          font-size: 1.5rem;
          margin-bottom: 1rem;
          color: #a6ff00;
        }

        .footerPageContainerChildContactInfoUL {
          list-style: none;
          margin-left: 0;
          padding-left: 0;

          li {
            margin-bottom: 0.5rem;
            display: flex;
            align-items: center;

            svg {
              margin-right: 0.5rem;
            }
          }
        }
      }
    }
  }

  .footerPagebottom {
    background-color: #222222;
    padding: 1rem 0;
    margin-bottom: -50px;

    .footerPagebottomContainer {
      display: flex;
      justify-content: center;

      .footerPagebottomContainerChild {
        display: flex;
        align-items: center;

        .footerPagebottomContainerChildCopyRight {
          p {
            margin: 0;
            font-size: 0.875rem;
            text-align: left;
          }
        }

        .footerPagebottomContainerChildOwnerShip {
          p {
            margin: 0;
            font-size: 0.875rem;
            text-align: right;

            a {
              color: #ffffff;
              text-decoration: none;
              transition: color 0.3s ease;

              &:hover {
                color: #ff0000;
              }
            }
          }
        }
      }
    }
  }

  @media (max-width: 768px) {
    .footerPageContainer {
      flex-direction: column;

      .footerPageContainerChild {
        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 2rem;
        }
      }
    }
  }

  @media (max-width: 480px) {
    .footerPageContainer {
      padding-left: 0px;
      .footerPageContainerChild {
        .footerPageContainerChildHeadingPara,
        .footerPageContainerChildLinks,
        .footerPageContainerChildContactInfo {
          text-align: center;

          h4 {
            font-size: 1.25rem;
          }
        }

        .footerlinksContainer {
          flex-direction: column;

          .footerPageContainerChildUL-links,
          .footer-links {
            margin-right: 0;
            margin-bottom: 1rem;
          }
        }
      }
    }
  }
}
