.social-media {
  text-align: center;
  background-color: #468ce7;
  margin: 0 auto;
  margin-top: 20px;
  padding: 20px 0;
  border-radius: 20px;
  width: 90%;




  .social-media-heading {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: #ffffff;
  }

  .social-links {
    display: flex;
    justify-content: center;
    gap: 40px;

    a {
      display: inline-block;
      margin: 0 0.5rem;
      color: #ffffff;
      font-size: 2rem;
      transition: color 0.3s ease;
      &:hover {
        color: #d19a9a;
      }
    }
  }

  @media (max-width: 768px) {
    .social-links {
      a {
        font-size: 1.5rem;
      }
    }
  }

  @media (max-width: 480px) {
    .social-links {
      gap: 10px;
      a {
        margin: 0.5rem 0;
      }
    }
  }
}
