@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

.featuredproduct-heading{
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
}
.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  margin-bottom: 20px;

  @media (max-width: 768px) {
    gap: 10px;
  }

  @media (max-width: 480px) {
    justify-content: flex-start;
    margin-top: 20px;
  }
}

.product {
  position: relative;
  width: 220px;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  transition: transform 0.3s;
  cursor: pointer;

  @media (max-width: 768px) {
    width: 180px;
    padding: 10px;
  }

  @media (max-width: 480px) {
    width: 100%;
    padding: 10px;
    border: none;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  }

  &:hover {
    transform: scale(1.05);
  }

  img {
    width: 100%;
    height: auto;
    border-radius: 5px;
    object-fit: cover;
    transition: opacity 0.3s;
  }

  h3 {
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 5px;
  }

  button {
    display: block;
    width: 100%;
    padding: 8px 0;
    background-color: #007bff;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
    cursor: pointer;
    a{
        color: #fff;
        text-decoration: none;
    }

    &:hover {
      background-color: #0056b3;
    }
  }
}
