.hero-section {
    position: relative;
    overflow: hidden;
    height: 500px;
    display: flex;
    align-items: center;
  }
  
  .hero-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 1;
  }
  
  .hero-title {
    font-size: 4rem;
    color: #1c1c1c;
    margin-bottom: 1rem;
  }
  
  .hero-description {
    font-size: 1.5rem;
    color: #1c1c1c;
    margin-bottom: 1.5rem;
  }
  
  @keyframes pulse {
    0% {
      transform: scale(1);
      background-color: #fbff00;
    }
    50% {
      transform: scale(1.1);
      background-color: #ff6600; 
    }
    100% {
      transform: scale(1);
      background-color: #fbff00;
    }
  }
  
  .hero-button {
    background-color: #fbff00;
    color: #2e2d2d;
    font-weight: 700;
    text-transform: uppercase;
    padding: 1rem 2rem;
    font-size: 1.5rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.25s ease;
    animation: pulse 2s infinite;
  }
  
  .hero-button:hover {
    background-color: #aeb124;
    color: #000000;
    animation: none; 
  }
  
  
  .hero-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    opacity: .2;
    top: 0;
    left: 0;
    z-index: 0;
  }
  
  @media (max-width: 770px) {
    
    .hero-title {
      font-size: 2rem;
      padding: 0;
    }
  
    .hero-description {
      font-size: 1rem;
      padding: 0;
    }
  
    .hero-button {
      font-size: 1rem;
    }
  }
  