.AdminOrders {
  display: flex;
  width: 100%;
  @media (max-width: 1024px) {
    flex-direction: column;
  }
  .AdminOrdersLeft {
    flex: 0 0 20%;
    padding-right: 10px;

    @media (max-width: 1024px) {
      flex: 0 0 40%;
    }

    @media (max-width: 1024px) {
      flex: 0 0 50%;
      flex: none;
      padding-right: 20px;
      width: 100%;
    }
  }

  .AdminOrdersRight {
    flex: 1;
    @media (max-width: 1024px) {
      flex: none;
    }

    .AdminOrdersRight-Heading {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .AdminOrdersRight_table_Container {
      margin-bottom: 30px;

      .AdminOrdersRight_table {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 10px;
        th,
        td {
          padding: 10px;
          border: 1px solid #ddd;
          @media (max-width: 600px) {
            padding: 0;
          }
        }

        th {
          background-color: #fffefe;
          font-weight: bold;
          @media (max-width: 600px) {
            font-weight: 400;
          }
        }

        tbody {
          tr {
            td {
              .ant-select-selector {
                @media (max-width: 600px) {
                    font-size: 10px;
                    padding: 2px;
                  }
              }
              @media (max-width: 600px) {
                font-size: 10px;
              }
            }
          }
        }
        tbody tr:nth-child(even) {
          background-color: #dacccc;
        }
      }

      .AdminOrdersRight_table-OrderData {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 20px;
        width: 100%;

        .AdminOrdersRight_table-OrderData-Card {
          display: flex;
          background-color: #fff4f4;
          padding: 10px;
          border-radius: 4px;
          @media (max-width: 600px) {
            flex-direction: column;
          }
          .AdminOrdersRight_table-OrderData-Card-Img-Container {
            width: 20%;
            height: 300px;
            overflow: hidden;
            margin-right: 10px;

            @media (max-width: 1550px) {
              height: 200px;
            }

            @media (max-width: 600px) {
              width: 50%;
            }

            .AdminOrdersRight_table-OrderData-Card-img {
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }

          .AdminOrdersRight_table-OrderData-Card-Details {
            flex-grow: 1;
            width: 90%;

            .AdminOrdersRight_table-OrderData-Card-Details-Name {
              font-size: 16px;
              font-weight: bold;
              margin-bottom: 5px;
              width: 80%;
            }

              .AdminOrdersRight_table-OrderData-Card-Details-Price {
                font-size: 14px;
                font-weight: bold;
                margin-right: 10px;
              }
            
          }
        }
      }
    }
  }
}
