@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

.special-offers {
  margin-top: 50px;
background-color: #f1eaea;

  .special-offers-heading{
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 20px;
    padding-top: 20px;
}

  .offer-list {
    display: flex;
    justify-content: space-between;
  }

  .offer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    transition: transform 0.3s;
    cursor: pointer;

    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    }

    h3 {
      font-family: 'Open Sans', sans-serif;
      font-size: 18px;
      margin-top: 10px;
      margin-bottom: 5px;
    }
  }

  .offer-image {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 75%;
    overflow: hidden;
    border-radius: 5px;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    .offer-discount {
      position: absolute;
      top: 10px;
      right: 10px;
      padding: 5px 10px;
      background-color: #007bff;
      color: #fff;
      font-family: 'Open Sans', sans-serif;
      font-size: 16px;
      border-radius: 5px;
    }
  }

  @media (max-width: 768px) {
    .offer-list {
      flex-wrap: wrap;
      gap: 20px;
    }

    .offer {
      width: 48%;
    }
  }

  @media (max-width: 480px) {
    .offer {
      width: 100%;
    }
  }
}
