.ProductsAdminContainer {
    display: flex;
    
    .ProductsAdminContainer-Left {
      flex: 0 0 20%;
      padding-right: 10px;
    }
  
    .ProductsAdminContainer-Right {
      flex: 1;
      
      .ProductsAdminContainer-Right-Heading {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 20px;
      }
  
      .ProductsAdminContainer-Right-Content {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        grid-gap: 20px;
        
        .ProductsAdminContainer-Right-Content-product-link {
          text-decoration: none;
          color: inherit;
        }
  
        .ProductsAdminContainer-Right-Content-Card {
          border: 1px solid #ddd;
          border-radius: 4px;
          padding: 10px;
          background-color: #fff;
          transition: box-shadow 0.3s;
          overflow: hidden;
          
          &:hover {
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
          }
          
          .ProductsAdminContainer-Right-Content-Card-Img-Container {
            display: flex;
            justify-content: center;
            align-items: center;
            height: 150px;
          }
  
          .ProductsAdminContainer-Right-Content-Card-img {
            width: auto;
            height: 100%;
            object-fit: contain;
            max-width: 100%;
            max-height: 100%;
          }
          
          .ProductsAdminContainer-Right-Content-Card-body {
            padding: 10px;
            
            .ProductsAdminContainer-Right-Content-Card-Name {
              font-size: 18px;
              font-weight: bold;
              margin-bottom: 5px;
            }
            
            .ProductsAdminContainer-Right-Content-Card-text {
              font-size: 14px;
              color: #777;
            }
          }
        }
      }
    }
  }
  
  @media (max-width: 600px) {
    .ProductsAdminContainer {
      flex-direction: column;
      
      .ProductsAdminContainer-Left {
        flex: none;
      }
      
      .ProductsAdminContainer-Right {
        flex: none;
        margin-top: 20px;
      }
    }
  }
  