/* CancellationRefund.scss */

.cancellation-refund-page {
    background-color: #f7f7f7;
    padding: 50px 0;
    text-align: center;
  }
  
  .cancellation-refund-container {
    max-width: 800px;
    margin: 0 auto;
    background-color: #fff;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  }
  
  .title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .intro {
    font-size: 16px;
    margin-bottom: 30px;
  }
  
  .section {
    margin-bottom: 40px;
  }
  
  .section-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .section-content {
    font-size: 16px;
  }
  
  @media (max-width: 768px) {
    .cancellation-refund-container {
      padding: 20px;
    }
  
    .title {
      font-size: 20px;
    }
  
    .intro {
      font-size: 14px;
    }
  
    .section-title {
      font-size: 18px;
    }
  
    .section-content {
      font-size: 14px;
    }
  }
  