.privacy-policy-page {
    background-color: #f7f7f7;
    padding: 60px 0;
  
    .privacy-policy-container {
      max-width: 900px;
      margin: 0 auto;
      text-align: center;
  
      h2 {
        font-size: 40px;
        font-weight: 700;
        margin-bottom: 40px;
        font-family: 'Montserrat', sans-serif;
        color: #2c3e50;
        text-transform: uppercase;
  
        &::before,
        &::after {
          content: '';
          display: block;
          width: 40px;
          height: 3px;
          background-color: #2c3e50;
          margin: 10px auto;
        }
      }
  
      p {
        font-size: 18px;
        color: #555;
        margin-bottom: 20px;
        font-family: 'Open Sans', sans-serif;
      }
  
      h3 {
        font-size: 24px;
        font-weight: 600;
        color: #2c3e50;
        margin-bottom: 10px;
        font-family: 'Montserrat', sans-serif;
  
        &::before,
        &::after {
          content: '';
          display: block;
          width: 40px;
          height: 3px;
          background-color: #2c3e50;
          margin: 10px auto;
        }
      }
  
      ul {
        margin-top: 10px;
        list-style: none;
        li {
          font-size: 16px;
          line-height: 1.6;
          color: #555;
          font-family: 'Open Sans', sans-serif;
        }
      }
    }
  }
  