.image-slider {
  position: relative;
  width: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  margin-bottom: 20px;

  .img-slider-heading{
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    margin-top: 20px;
  }
  .image-slider-heading-para{
    text-align: center;
    padding: 0 50px;

    @media (max-width: 767px) {
      padding: 0;
    }
  }
  .slider-image {
    display: block;
    width: 100%;
    height: 420px;
    object-fit: cover;
    transition: transform 1s ease, opacity 1s ease;
    @media (max-width: 767px) {
      padding: 0;
    }
    &.active {
      opacity: 1;
    }

    &:not(.active) {
      opacity: 0.8;
    }
    @media (max-width: 770px) {
      height: 320px;
    }
    @media (max-width: 450px) {
      height: 200px;
    }
  }

  .slider-controls {
    position: absolute;
    top: 60%;
    transform: translateY(-50%);
    display: flex;
    justify-content: space-between;
    width: 100%;
    z-index: 1;

    .slider-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      background-color: transparent;
      border: none;
      color: #fff;
      font-size: 20px;
      cursor: pointer;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: rgba(255, 255, 255, 0.3);
      }

      &-prev {
        margin-right: 10px;
      }

      &-next {
        margin-left: 10px;
      }
    }

    @media (max-width: 770px) {
      top: 68%;
    }
    @media (max-width: 500px) {
      top: 80%;
    }
  }

  .slider-indicators {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    width: 100%;
    z-index: 1;

    .slider-indicator {
      display: block;
      width: 10px;
      height: 10px;
      margin: 0 5px;
      border-radius: 50%;
      background-color: #fff;
      opacity: 0.5;
      cursor: pointer;
      transition: opacity 0.3s ease;

      &.active {
        opacity: 1;
      }
    }
  }

  @media (max-width: 767px) {
    .slider-controls {
      .slider-btn {
        width: 30px;
        height: 30px;
        font-size: 16px;
      }
    }

    .slider-indicators {
      .slider-indicator {
        width: 8px;
        height: 8px;
        margin: 0 3px;
      }
    }
  }
}
