.Userdashboard {
    display: flex;

    .Userdashboard-Left {
      flex: 0 0 20%;
      padding-right: 10px;
    }
  
    .Userdashboard-Right {
      flex: 1;
      margin-top: 50px;
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      padding: 20px;
  
      .Userdashboard-Right-Content {
        display: flex;
        flex-direction: column;
  
        .Userdashboard-Right-Content-Name,
        .Userdashboard-Right-Content-Email,
        .Userdashboard-Right-Content-Address {
          font-size: 18px;
          margin-bottom: 20px;
        }
      }
    }
  }
  
  @media (max-width: 600px) {
    .Userdashboard {
      flex-direction: column;
  
      .Userdashboard-Left {
        flex: none;
      }
  
      .Userdashboard-Right {
        flex: none;
        margin-top: 20px;
      }
    }
  }
  