$primary-color: red; // Import your custom variables here

.productPage {
  padding: 0;
  // Define the styles for the banner image
  .productPage-banner-img {
    width: 100%;
    height: 300px;
    margin-top: -2px;
    object-fit: cover;
    @media (max-width: 1024px) {
      margin-top: -6px;
      height: 250px;
    }
    @media (max-width: 991px) {
      margin-top: -12px;
    }
    @media (max-width: 768px) {
      height: 200px;
    }
    @media (max-width: 550px) {
      height: 120px;
    }
  }

  .productPagecontainer {
    display: flex;
    @media (max-width: 900px) {
      flex-direction: column;
    }

    .productPagefiltersLeft {
      padding: 15px;
      width: 20%;
      @media (max-width: 900px) {
       width: 100%;
      }
      .productPagefiltersHeading {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 10px;
      }

      .productPagefiltersCategories {
        margin-bottom: 20px;

        // Style the checkboxes
        .ant-checkbox-wrapper {
          // display: block;
          margin-bottom: 10px;
          font-size: 16px;
        }
      }

      .productPagefiltersPrices {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
      
        .filterSlider {
          display: flex;
          align-items: center;
      
          span {
            margin: 0 10px;
          }
      
          .ant-slider {
            flex: 1;
          }
        }
      
        .SortBtns {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          margin-top: 10px;
      
           button {
            margin-bottom: 10px;
            padding: 8px 16px;
            font-size: 14px;
            background-color: #4b4141;
            color: #fff;
            border: none;
            cursor: pointer;
            border-radius: 4px;
            transition: background-color 0.3s ease;
            
            &:last-child {
              margin-right: 0;
            }
          
            &:hover {
              background-color: rgb(63, 52, 52);
            }
          
            &:focus {
              outline: none;
              box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
            }
          }
          
        }
      }
      

      .productPagefiltersResetBtnContainer {
        text-align: center;

        // Style the reset button
        .productPagefiltersResetBtn {
          background-color: $primary-color;
          color: #fff;
          padding: 8px 16px;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          font-size: 16px;

          &:hover {
            background-color: darken($primary-color, 10%);
          }
        }
      }
    }

    .productPageContentRight {
      width: 90%;
      padding: 0 20px;

      @media (max-width: 550px) {
        width: 100%;
        padding: 0 10px;
      }

      .productPageContentHeading {
        font-size: 28px;
        font-weight: bold;
        margin-bottom: 20px;
      }

      .productPageContentProducts {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
        @media (max-width: 1024px) {
          grid-template-columns: repeat(3, 1fr);
        }
        @media (max-width: 950px) {
          grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 550px) {
          grid-template-columns: 1fr;
        }
        .productPageContentProductsCard {
          background-color: #fff;
          border-radius: 4px;
          padding: 10px;
          box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
          animation: fade-in 0.5s ease-in-out;

          .productPageCard-image {
            position: relative;
            margin-bottom: 10px;
            overflow: hidden;
            width: 100%;
            height: 320px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            // Style the product image
            .productPageCardImg {
              width: 80%;
              height: 100%;
              object-fit: contain;
              border-radius: 4px;
              transition: transform 0.3s ease-in-out;

              &:hover {
                transform: scale(1.1);
              }
            }

            // Style the "Add to Cart" button
            .productPageCard-AddtocartIcon {
              position: absolute;
              top: 10px;
              right: 10px;
              background-color: #5640b9;
              color: #fff;
              border: none;
              border-radius: 50%;
              width: 40px;
              height: 40px;
              display: flex;
              align-items: center;
              justify-content: center;
              cursor: pointer;
              transition: background-color 0.3s;

              &:hover {
                background-color: darken($primary-color, 10%);
              }
            }
          }

          .productPageCard-body {
            .productPageCard-title {
              font-size: 18px;
              font-weight: bold;
              margin-bottom: 10px;
            }

            .productPageCard-Prices {
              margin-bottom: 10px;

              .productPageCard-price {
                font-size: 20px;
                font-weight: bold;
                color: $primary-color;
                margin-right: 10px;
              }

              .productPageCard-Oldprice {
                font-size: 16px;
                color: #999;
                text-decoration: line-through;
                margin-right: 10px;
              }

              .productPageCard-Discount {
                font-size: 16px;
                background-color: #1c1c1c;
                color: #fff;
                padding: 2px 8px;
                border-radius: 10px;
              }
            }

            .productPageCard-Rating {
              font-size: 16px;
              margin-bottom: 5px;

              .productPageCard-Rating-Span{
                background-color: $primary-color;
                color: #fff;
                padding: 2px 8px;
                border-radius: 12px;
              }
            }
          }
        }
      }

      .productPageContentLoadingContainer {
        text-align: center;
        margin-top: 20px;

        .productPageContentLoadingContainerBtn {
          background-color: $primary-color;
          color: #fff;
          padding: 8px 16px;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          font-size: 16px;

          &:hover {
            background-color: darken($primary-color, 10%);
          }
        }
      }
    }
  }
}

// Animation keyframes
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
